import React from 'react'
import { replace } from 'ramda'
import { Row, Col, Form } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { FileImageOutlined, ProfileOutlined } from '@ant-design/icons'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb } from '@/hooks/useApp'
import { useReportList } from '@/data/useReport'
import Select from '@/components/select'
import Input from '@/components/input'
import { RangePicker } from '@/components/datepicker'
import { Page } from '@/components/layout'
import Table from '@/components/query-table'
import Button from '@/components/button'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'

export default function ReportList() {
  useTitle('清單查詢')
  useBreadcrumb([{ name: '清單查詢' }])

  const [form] = Form.useForm()

  const [reportList] = useReportList()

  console.log(reportList)

  const handleOk = () => {}

  const handleToReport = (evt: React.MouseEvent<HTMLElement>) => {
    history.push(replace(':id', `${evt.currentTarget.dataset.id}`)(REPORT_EDIT))
  }

  const columns: ColumnProps<any>[] = [
    {
      title: '編號',
      dataIndex: 'id',
      render: (_, __, idx) => idx + 1,
    },
    { title: '申請序號', dataIndex: 'serialNo' },
    { title: '姓名', dataIndex: 'HNAMEC' },
    { title: '病理號', dataIndex: 'ORWORKNO' },
    { title: '病例號', dataIndex: 'HHISNUM' },
    { title: '病房', dataIndex: 'HBED' },
    { title: '性別', dataIndex: 'HSEX' },
    { title: '簽收日期', dataIndex: 'checkDate' },
    { title: '收費碼', dataIndex: 'createAt' },
    { title: '科別', dataIndex: 'CSECT' },
    { title: '狀態', dataIndex: 'state' },
    { title: '醫囑', dataIndex: 'ORPROCED' },
    {
      title: '報告',
      dataIndex: 'x',
      render: (v, r) => (
        <ProfileOutlined
          data-id={r.id}
          className="cursor-pointer text-lg"
          onClick={handleToReport}
        />
      ),
    },
    {
      title: '影像',
      dataIndex: 'createAt',
      render: (v, r) => (
        <FileImageOutlined data-id={r.id} className="cursor-pointer text-lg" />
      ),
    },
    // { title: '入賬日期', dataIndex: 'creditDate' },
    // { title: '入賬時間', dataIndex: 'creditTime' },
  ]

  return (
    <Page style={{ height: 'calc(100vh - 203px)' }}>
      <Row justify="center">
        <Col span={20}>
          <div className="bg-white p-12 mb-8 rounded-lg shadow-lg">
            <Form
              form={form}
              layout="vertical"
              hideRequiredMark={true}
              colon={false}>
              <Row gutter={12}>
                <Col span={8}>
                  <Form.Item label="病例號" name="no">
                    <Input
                      addonBefore={
                        <Select style={{ width: 100 }} defaultValue="1">
                          <Select.Option value="1">申請序號</Select.Option>
                          <Select.Option value="2">病理號</Select.Option>
                          <Select.Option value="3">病例號</Select.Option>
                        </Select>
                      }
                    />
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label="查詢類別" name="doctor">
                    <Select allowClear>
                      <Select.Option value={'醫囑'}>醫囑</Select.Option>
                      <Select.Option value={'報告'}>報告</Select.Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label="簽收日期" name="createAt">
                    <RangePicker className="w-full" />
                  </Form.Item>
                </Col>
              </Row>

              <Row gutter={12}>
                <Col span={8}>
                  <Form.Item label="報告類別" name="doctor">
                    <Select allowClear>
                      <Select.Option value={'細胞學'}>細胞學</Select.Option>
                      <Select.Option value={'切片'}>切片</Select.Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label="報告狀態" name="state">
                    <Select allowClear>
                      <Select.Option value={1}>未輸入</Select.Option>
                      <Select.Option value={2}>複審</Select.Option>
                      <Select.Option value={3}>暫存</Select.Option>
                      <Select.Option value={4}>不確定</Select.Option>
                      <Select.Option value={5}>正式</Select.Option>
                      <Select.Option value={6}>全部</Select.Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={8}>
                  <Form.Item label=" ">
                    <Button
                      color="primary"
                      className="ml-3"
                      style={{ width: 120 }}
                      onClick={handleOk}>
                      確認
                    </Button>
                  </Form.Item>
                </Col>
              </Row>
            </Form>
          </div>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <Table
            rowKey="id"
            dataSource={{ content: reportList, total: reportList.length }}
            columns={columns}
          />
        </Col>
      </Row>
    </Page>
  )
}
